<template>
<div>
 <Suspense>
   <template #default>
     <SideLastNews :menu="list"/>
   </template>
   <template #fallback>
     <LoaderComponentTwo/>
   </template>
 </Suspense>
</div>
</template>

<script>
import{reactive,onMounted,ref} from "vue";
import EndPoints from "@/service/EndPoints";
import{defineAsyncComponent} from "vue";
import {handleError} from "@/components/composable/handleError";
const LoaderComponentTwo=defineAsyncComponent(()=>import('@/components/composable/LoaderComponentTwo'))
const SideLastNews=defineAsyncComponent(()=>import('@/components/main/LastNews/SideLastNews'));

export default {
  name: "AllNews",
  components: {LoaderComponentTwo, SideLastNews},
  setup(){
    const index=ref(0)
    const list=reactive([])
    const total=onMounted(async()=>{

      await EndPoints.getAllNews()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data)
        }
      })
      .catch((error)=>{
        handleError(error)
      })
    })
    return{
      list,
      total,
      index
    }
  }
}
</script>

<style lang="scss" scoped>
@mixin myText($item){
  @if($item >= 50){
    @content;
  }
}
#message{
  @include myText(50){
    overflow: hidden;
    text-overflow: ellipsis;
    white-space:pre-line;
    line-height: normal;
  }
}
.scroll{
  height: auto;
  overflow-x: hidden;
  overflow-y: scroll;
  background-color: #fff;
}
#scroll::-webkit-scrollbar{
  width: 5px;
  background-color: cornsilk;
}
#scroll::-webkit-scrollbar-track{
  background-color: lightgrey;
  box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
#scroll::-webkit-scrollbar-thumb{
  background-color: grey;
}
</style>